import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Tooltip, Select, Collapse, TreeSelect, Modal, Icon, Col } from "antd";
import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/item/item/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange"

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const redirectTo = "/dashboard/setup/menus";

class Item extends Component {
  state = {
    filter: {
      id_item_category: null,
      item_type: null,
      is_active: null,
      sort_by: "item_code",
      sort_type: "asc",
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getItems } = this.props;
    getItems();
  }

  componentDidUpdate(prevProps) {
    // Display error notif
    if (this.props.item.error) {
      notification("error", this.props.item.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.item.success) {
      notification("success", this.props.item.success);
      this.props.clearSuccess();
    }

    if (prevProps.item.loadingImport && !this.props.item.loadingImport) {
      Modal.destroyAll();
    }
  }

  //import from Raptor
  importFromRaptor = () => {
    const { importRaptorItems } = this.props;
    const { raptor_import } = this.props.item.info_message;
    importRaptorItems();

    Modal.info({
      title: raptor_import.title,
      content: raptor_import.content,
      okText: raptor_import.ok_button,
      okButtonProps: {
        style: {
          display: "none"
        }
      },
      keyboard: false,
      icon: <Icon type="loading" />
    });
  }

  // Handle search
  handleSearch = value => {
    const { getItems } = this.props;
    this.setState({
      filter: {
        id_item_category: null,
        item_type: null,
        is_active: null,
        sort_by: "item_code",
        sort_type: "asc",
        pagination: null
      }
    });
    getItems(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getItems } = this.props;
    this.setState({
      filter: {
        id_item_category: null,
        item_type: null,
        is_active: null,
        sort_by: "item_code",
        sort_type: "asc",
        pagination: null
      }
    });
    this.setState({ search: null });
    getItems();
  };

  //Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getItems } = this.props;
    //const { current_page } = this.props.item;
    const { filter } = this.state;
    let string = "";

    Object.keys(filter).map(key => filter[key]
      ? string
        ? (string += `&${key}=${filter[key]}`)
        : (string += `${key}=${filter[key]}`)
      : null);
    //console.log(string);
    this.setState({ search: null });
    getItems(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getItems } = this.props;
    this.setState({
      filter: {
        id_item_category: null,
        item_type: null,
        is_active: null,
        sort_by: "item_code",
        sort_type: "asc",
        pagination: null
      }
    });
    this.setState({ search: null });
    getItems();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateItem(id);
    } else {
      this.props.deactivateItem(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getItems } = this.props;
    const { search } = this.props.item;
    const { filter } = this.props.item;
    getItems(page, search, filter);
  };

  listAction = {
    title: "",
    dataIndex: "id_item",
    rowKey: "id_item",
    //width: 100,
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active === -1
          ? null
          : row.is_active === 1
            ? (
              <Popconfirms
                title={<IntlMessages id="items.popconfirm.deactivate" />}
                onConfirm={this.handleRecord.bind(this, "deactivate", text)}
                okText={getLanguage({ id: "yes" })}
                cancelText={getLanguage({ id: "no" })}
              >
                <Tooltip
                  placement="topRight"
                  title={<IntlMessages id="tooltip.deactivate" />}
                >
                  <Button size="small" className="invoiceDltBtn">
                    <i className="ion-close-circled" />
                  </Button>
                </Tooltip>
              </Popconfirms>
            ) : (
              <Popconfirms
                title={<IntlMessages id="items.popconfirm.activate" />}
                onConfirm={this.handleRecord.bind(this, "activate", text)}
                okText={getLanguage({ id: "yes" })}
                cancelText={getLanguage({ id: "no" })}
              >
                <Tooltip
                  placement="topRight"
                  title={<IntlMessages id="tooltip.activate" />}
                >
                  <Button size="small" className="invoiceRstBtn">
                    <i className="ion-refresh" />
                  </Button>
                </Tooltip>
              </Popconfirms>
            )}
      </div>
    )
  };

  render() {
    // console.log(this.props.item);
    // console.log(this.state.filter);
    const columns = [
      {
        title: getLanguage({ id: "items.code" }),
        dataIndex: "item_code",
        rowKey: "item_code",
        width: "20%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "item_name",
        rowKey: "item_name",
        width: "30%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "sidebar.itemCategory" }),
        dataIndex: "item_category.item_category_name",
        rowKey: "item_category.item_category_name",
        width: "30%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "items.itemType" }),
        dataIndex: "item_type",
        rowKey: "item_type",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "primary";
              display = getLanguage({ id: "inventory" });
              break;
            case 2:
              className = "ordered";
              display = getLanguage({ id: "nonInventory" });
              break;
            case 3:
              className = "invoiced";
              display = getLanguage({ id: "grouping" });
              break;
            case 4:
              className = "closed";
              display = getLanguage({ id: "service" });
              break;
            case 5:
              className = "cancelled";
              display = getLanguage({ id: "roles.tab.asset" });
              break;
            default:
              className = "nonactive";
              display = getLanguage({ id: "undefined" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      // {
      //   title: "Barcode",
      //   dataIndex: "barcode",
      //   rowKey: "barcode",
      //   width: "15%",
      //   render: text => <span>{text}</span>
      // },
      // {
      //   title: "SKU",
      //   dataIndex: "sku",
      //   rowKey: "sku",
      //   width: "15%",
      //   render: text => <span>{text}</span>
      // },
      // {
      //   title: "SKU Name",
      //   dataIndex: "sku_name",
      //   rowKey: "sku_name",
      //   width: "15%",
      //   render: text => <span>{text}</span>
      // },
      // {
      //   title: "Registered at",
      //   dataIndex: "registered_at.location_name",
      //   rowKey: "registered_at.location_name",
      //   width: "10%",
      //   render: text => <span>{text}</span>
      // },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "is_active",
        rowKey: "is_active",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "active";
            display = getLanguage({ id: "active" });
          }
          else if (text === -1) {
            className = "Primary";
            display = getLanguage({ id: "new" });
          }
          else {
            className = "nonactive";
            display = getLanguage({ id: "inactive" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    // Data setup
    const { items, raptor_module, loadingImport } = this.props.item;
    // const { importRaptorItems } = this.props;
    //console.log(this.props.item); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (permissions.includes("Update Item") && columns.length === 5) {
      columns.push(this.listAction);
    }

    Object.keys(items).map((item, index) => {
      return dataSource.push({
        ...items[item],
        key: item
      });
    });

    // Pagination Setup
    const { current_page, page_size, total, item_categories } = this.props.item;

    const loop = array =>
      array.map(data => {
        let perData = {
          title: data.item_category_code + " (" + data.item_category_name + ")",
          value: data.id_item_category,
          key: data.id_item_category,
          children: data.children_active ? loop(data.children_active) : null
        };

        return perData;
      });

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="items.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Get Import Raptor Items") && raptor_module === 1 ? (
                <Popconfirms
                  title={<IntlMessages id="items.popconfirm.raptorImport" />}
                  onConfirm={() => this.importFromRaptor()}
                  okText="Yes"
                  cancelText="No"
                >
                  <Button
                    type="default"
                    style={{ backgroundColor: "#00B16A", color: "#FFFFFF", }}
                    loading={loadingImport}
                  >
                    <IntlMessages id="items.button.raptorImport" />
                  </Button>
                </Popconfirms>
              ) : null}
              {permissions.includes("Create Item") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button type="primary" className="mateAddInvoiceBtn" style={{ marginLeft: "5px" }}>
                    <IntlMessages id="items.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={getLanguage({ id: "items.code" }) + ", " + getLanguage({ id: "name" }) + ", " + getLanguage({ id: "items.barcode" }) + ", " + getLanguage({ id: "items.sku" }) + ", " + getLanguage({ id: "items.skuName" })}
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <div className="isoFilterContent">
                  <Col span={12}>
                    <label>
                      <IntlMessages id="items.itemCategory" />
                    </label>
                    <br />

                    <TreeSelect
                      placeholder={
                        <IntlMessages id="items.select.itemCategory" />
                      }
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_item_category"
                      )}
                      style={{ width: "95%" }}
                      allowClear={true}
                      showSearch={true}
                      treeNodeFilterProp="title"
                      dropdownStyle={{ maxHeight: 300, overflow: "auto" }}
                      treeData={loop(item_categories)}
                      treeDefaultExpandAll
                      loading={this.props.item.loading}
                      value={
                        this.state.filter.id_item_category
                          ? this.state.filter.id_item_category
                          : []
                      }
                    />
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="items.itemType" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(this, "item_type")}
                      value={
                        this.state.filter.item_type
                          ? this.state.filter.item_type
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={<IntlMessages id="items.select.itemType" />}
                      optionFilterProp="children"
                      filterOption={true}
                      // loading={this.props.item.loading}
                      allowClear={true}
                    //showArrow={false}
                    >
                      <Option value={1}>{getLanguage({ id: "inventory" })}</Option>
                      <Option value={2}>{getLanguage({ id: "nonInventory" })}</Option>
                      <Option value={3}>{getLanguage({ id: "grouping" })}</Option>
                      <Option value={4}>{getLanguage({ id: "service" })}</Option>
                      {/* <Option value={5}>Asset</Option> */}
                    </Select>
                  </Col>
                </div>
                <div
                  className="isoFilterContent"
                  style={{ paddingTop: "10px" }}
                >
                  <Col span={12}>
                    <Col span={8}>
                      <label>
                        <IntlMessages id="sortBy" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "sort_by"
                        )}
                        value={
                          this.state.filter.sort_by
                            ? this.state.filter.sort_by
                            : null
                        }
                        style={{ width: "90%" }}
                        optionFilterProp="children"
                        filterOption={true}
                        loading={this.props.item.loading}
                      //allowClear={true}
                      //showArrow={false}
                      >
                        <Option key={1} value="item_code">
                          PLU
                        </Option>
                        <Option key={2} value="item_name">
                          <IntlMessages id="name" />
                        </Option>
                      </Select>
                    </Col>

                    <Col span={8}>
                      <label>
                        <IntlMessages id="sortType" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "sort_type"
                        )}
                        value={
                          this.state.filter.sort_type
                            ? this.state.filter.sort_type
                            : null
                        }
                        style={{ width: "90%" }}
                        optionFilterProp="children"
                        filterOption={true}
                        loading={this.props.item.loading}
                      //allowClear={true}
                      //showArrow={false}
                      >
                        <Option key={1} value="asc">
                          <IntlMessages id="ascending" />
                        </Option>
                        <Option key={2} value="desc">
                          <IntlMessages id="descending" />
                        </Option>
                      </Select>
                    </Col>

                    <Col span={8}>
                      <label>
                        <IntlMessages id="status" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "is_active"
                        )}
                        value={
                          this.state.filter.is_active
                            ? this.state.filter.is_active
                            : null
                        }
                        style={{ width: "85%" }}
                        optionFilterProp="children"
                        filterOption={true}
                        loading={this.props.item.loading}
                        allowClear={true}
                      //showArrow={false}
                      >
                        <Option key={1} value={1}>
                          {getLanguage({ id: "active" })}
                        </Option>
                        <Option key={2} value={-1}>
                          {getLanguage({ id: "new" })}
                        </Option>
                        <Option key={3} value={"0"}>
                          {getLanguage({ id: "inactive" })}
                        </Option>
                      </Select>
                    </Col>
                  </Col>

                  <Col span={12} style={{ display: "flex" }}>
                    <Col span={8}>
                      <label>
                        <IntlMessages id="dataPerPage" />
                      </label>
                      <br />
                      <Select
                        showSearch
                        onChange={this.handleFilterChange.bind(
                          this,
                          "pagination"
                        )}
                        value={
                          this.state.filter.pagination
                            ? this.state.filter.pagination
                            : page_size
                        }
                        style={{ width: "95%" }}
                        optionFilterProp="children"
                        filterOption={true}
                        loading={this.props.item.loading}
                      //allowClear={true}
                      //showArrow={false}
                      >
                        <Option key={1} value={10}>
                          10
                        </Option>
                        <Option key={2} value={25}>
                          25
                        </Option>
                        <Option key={3} value={50}>
                          50
                        </Option>
                        <Option key={4} value={100}>
                          100
                        </Option>
                      </Select>
                    </Col>

                    <Col span={8} style={{ marginTop: "auto" }}>
                      <Button
                        size="small"
                        type="primary"
                        style={{ marginRight: "10px" }}
                        onClick={this.handleFilter}
                      >
                        <IntlMessages id="filter.submit" />
                      </Button>
                      <Button onClick={this.handleResetFilter} size="small">
                        <IntlMessages id="filter.reset" />
                      </Button>
                    </Col>
                  </Col>
                </div>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {items.length === 0 && !this.props.item.loading ? (
              <Empty description={<IntlMessages id="items.table.noData" />} />
            ) : (
                <div className="isoInvoiceTable">
                  <Scrollbars style={{ width: "100%" }}>
                    <TableWrapper
                      dataSource={dataSource}
                      columns={columns}
                      loading={this.props.item.loading}
                      className="invoiceListTable"
                      bordered={true}
                      // Set pagination, jika ada page_size mengunakan pagination API
                      // jika tidak ada page_size menggunakan pagination bawaan
                      pagination={
                        page_size
                          ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                          : true
                      }
                    />
                  </Scrollbars>
                </div>
              )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    item: state.item,
    auth: state.auth
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(Item);
